
import VueRouter from 'vue-router'
import Login from '../Login.vue'
import Sign from '../pages/Sign.vue'
import TimeTable from '../pages/TimeTable.vue'
import Rule from '../pages/Rule.vue'
import Leader from '../pages/Leader.vue'
import AddStu from '../pages/AddStu.vue'
import StuList from '../pages/StuList.vue'
import LateRecord from '../pages/LateRecord.vue'
import ProxySign from '../pages/ProxySign.vue'
import UpdateTime from '../pages/UpdateTime.vue'
import LateRecordInf from '../pages/LateRecordInf.vue'
import SignRecord from "../pages/SignRecord.vue"
import Admin from "../pages/Admin.vue"

const router = new VueRouter({
  duplicateNavigationPolicy: 'ignore',
  routes:[
    {
      name:'login',
      path:'/',
      component:Login
    },

    {
      name:'sign',
      path:'/Sign',
      component:Sign,
      meta: {
        keepAlive: true, //添加这个作为标志符，表明该页面需要保留状态
      },
      children:[
        {
          name:'timetable',
          path:'/TimeTable',
          component:TimeTable
        },
        {
          name:'rule',
          path:'/Rule',
          component:Rule
        },
        {
          name:'lateRecordInf',
          path:'/LateRecordInf',
          component:LateRecordInf
        },
      ]
    },

    {
      name:'leader',
      path:'/Leader',
      component:Leader,
      children:[
        {
          name:'addstu',
          path:'/AddStu',
          component:AddStu,
        },
        {
          name:'stulist',
          path:'/StuList',
          component:StuList,
        },
        {
          name:'updatetime',
          path:'/UpdateTime',
          component:UpdateTime,
        },
        {
          name:'laterecord',
          path:'/LateRecord',
          component:LateRecord,
        },
        {
          name:'proxysign',
          path:'/ProxySign',
          component:ProxySign,
        },
        {
          name:'signRecord',
          path:'/SignRecord',
          component:SignRecord,
        },
        {
          name:'admin',
          path:'/Admin',
          component:Admin,
        }
      ]
    }

  ]
})

export default router
