<template>
  <div>
        <el-table
        :data="tableData"
        style="width: 80%;margin-left:11%;margin-top:0px">

        <el-table-column
            prop="classToClass"
            label="签到时间 / 星期"
            width="180">
                <template slot-scope="scope">
                    <span v-if="scope.row.classToClass === '1-2' ">7:40 - 8:00</span>
                    <span v-if="scope.row.classToClass === '3-4' ">9:40 - 10:00</span>
                    <span v-if="scope.row.classToClass === '5-6' ">13:10 - 13:30</span>
                    <span v-if="scope.row.classToClass === '7-8' ">14:40 - 15:00</span>
                    <span v-if="scope.row.classToClass === '9-10' ">16:40 - 17:00</span>
                </template>
        </el-table-column>

        <el-table-column
            prop="monday"
            label="星期一"
            width="200">
            <template slot-scope="scope">
                    <div v-if="scope.row.monday === 1" style="font-size:20px"> 无需签到</div>
                    <div v-else-if="scope.row.monday === 2" style="font-size:20px"><el-button icon="el-icon-check"></el-button></div>
                    <div v-else-if="scope.row.monday === 3" style="font-size:20px"><el-button icon="el-icon-close"></el-button></div>
                    <div v-else-if="scope.row.monday === 4" style="font-size:20px">未签</div>
                    <div v-else-if="scope.row.monday === 0"><el-button icon="el-icon-help" @click="sign(scope.row,'monday')"></el-button></div>
            </template>
        </el-table-column>

        <el-table-column
            prop="tuesday"
            label="星期二"
            width="200">
            <template slot-scope="scope">
                    <div v-if="scope.row.tuesday === 1" style="font-size:20px"> 无需签到</div>
                    <div v-else-if="scope.row.tuesday === 2" style="font-size:20px"><el-button icon="el-icon-check"></el-button></div>
                    <div v-else-if="scope.row.tuesday === 3" style="font-size:20px"><el-button icon="el-icon-close"></el-button></div>
                    <div v-else-if="scope.row.tuesday === 4" style="font-size:20px">未签</div>
                    <div v-else-if="scope.row.tuesday === 0"><el-button icon="el-icon-help" @click="sign(scope.row,'tuesday')"></el-button></div>
            </template>
        </el-table-column>

        <el-table-column
            prop="wednesday"
            label="星期三"
            width="200">
            <template slot-scope="scope">
                <div class="row-template">
                    <div v-if="scope.row.wednesday === 1" style="font-size:20px"> 无需签到</div>
                    <div v-else-if="scope.row.wednesday === 2" style="font-size:20px"><el-button icon="el-icon-check"></el-button></div>
                    <div v-else-if="scope.row.wednesday === 3" style="font-size:20px"><el-button icon="el-icon-close"></el-button></div>
                    <div v-else-if="scope.row.wednesday === 4" style="font-size:20px">未签</div>
                    <div v-else-if="scope.row.wednesday === 0"><el-button icon="el-icon-help" @click="sign(scope.row,'wednesday')"></el-button></div>
                </div>
            </template>
        </el-table-column>

        <el-table-column
            prop="thursday"
            label="星期四"
            width="200">
            <template slot-scope="scope">
                    <div v-if="scope.row.thursday === 1" style="font-size:20px"> 无需签到</div>
                    <div v-else-if="scope.row.thursday === 2" style="font-size:20px"><el-button icon="el-icon-check"></el-button></div>
                    <div v-else-if="scope.row.thursday === 3" style="font-size:20px"><el-button icon="el-icon-close"></el-button></div>
                    <div v-else-if="scope.row.thursday === 4" style="font-size:20px">未签</div>
                    <div v-else-if="scope.row.thursday === 0"><el-button icon="el-icon-help" @click="sign(scope.row,'thursday')"></el-button></div>
            </template>
        </el-table-column>

        <el-table-column
            prop="friday"
            label="星期五"
            width="200">
            <template slot-scope="scope">
                    <div v-if="scope.row.friday === 1" style="font-size:20px"> 无需签到</div>
                    <div v-else-if="scope.row.friday === 2" style="font-size:20px"><el-button icon="el-icon-check"></el-button></div>
                    <div v-else-if="scope.row.friday === 3" style="font-size:20px"><el-button icon="el-icon-close"></el-button></div>
                    <div v-else-if="scope.row.friday === 4" style="font-size:20px">未签</div>
                    <div v-else-if="scope.row.friday === 0"><el-button icon="el-icon-help" @click="sign(scope.row,'friday')"></el-button></div>
            </template>
        </el-table-column>

        </el-table>
  </div>
</template>

<script>
import axios from 'axios'
export default {
    data() {
        return {
        tableData: [],
        clickCount:0,
        }
    },
    mounted(){
        this.getTimeTable();
    },
    methods:{
        getTimeTable(){
            axios.get("/stuTableTime/"+window.localStorage.getItem('stuCode')).then((res)=>{
                this.tableData = res.data.data
            })
        },
        sign(row,day){
            this.clickCount++
            if(this.clickCount >= 3){
                if(this.clickCount > 3 && this.clickCount <= 6){
                    if(this.clickCount !== 6){
                        this.$message.warning("请勿点击多次!!!")
                    }
                    if(this.clickCount === 6){
                        this.$message.warning("十秒后才能点击!!!")
                    }
                    setTimeout(()=>{
                        this.clickCount = 0
                    },10000)
                }
            }else if(this.clickCount <= 3){
                
                axios.put("/stuTableTime/"+day,row).then((res)=>{
                    if(res.data.code === 202381){
                        this.$message.success(res.data.data)
                        this.getTimeTable();
                        axios.get("/signRecord/"+window.localStorage.getItem('stuName')).then((res)=>{
                        })
                    }else{
                        this.$message.error(res.data.data)
                    }
                })
            }
        }
    }
}
</script>

<style>
    tr{
        line-height: 50px;
    }
    .row-template {
        line-height: 80px; /* 设置行的高度为 50 像素 */
    }
    table td {
        text-align: center; /* 让单元格中的内容水平居中对齐 */
    }
</style>