<template>
  
    <div>
      <div v-show="this.$show">
        <el-container>

            <el-header style="height:65px">
                <span style="font-size:20px;padding:30px">QIDIAN</span>
            </el-header>

            <el-main>
                <div id="left">

                    <span id="saying">
                        起点工作室
                    </span>
                    <p>  
                        勤勤恳恳做人，
                    </p>
                    <p>
                        踏踏实实做事。
                    </p>
                    <p>
                        ----起点
                    </p>
                </div>
                <div id="middle">
                    <img src="./picture/OIP-C.jpg" id="picture">
                </div>
                <div id="right">
                    <span id="top1" >密码登录</span>

                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="50px" class="demo-ruleForm">
                        <el-form-item  prop="stuCode" style="margin-top:50px">
                            <el-input v-model="ruleForm.stuCode" style="width:270px;" placeholder="请输入学号"></el-input>
                        </el-form-item>
                        <el-form-item  prop="password">
                            <el-input v-model="ruleForm.password" type="password" style="width:270px;" placeholder="请输入密码"></el-input>
                        </el-form-item>
                    </el-form>
                    
                    <el-row style="padding-left:56px">
                    <el-button type="primary" round style="width:250px" @click="login('ruleForm')" :disabled="buttonClicked">登录</el-button>
                    </el-row>
                </div>
            </el-main>
    
            <el-footer style="height:66.9px">
                <span style="font-size:13px;margin-left:550px">
                    起点工作室位于图书馆二楼和电院11-507
                </span>
            </el-footer>
        </el-container>
       </div> 
        <div v-show="!(this.$show)">
            <keep-alive>
                    <router-view></router-view>
            </keep-alive>
        </div>
    </div>
  
</template>

<script>
import axios from 'axios'
import Sign from "./pages/Sign.vue"
import UAParser from 'ua-parser-js';

export default {
    
    name:'Login',
    components:{
        Sign,
    },
    data() {
      return {
        ruleForm: {},
        rules: {
          cord: [
            { required: true, message: '请输入学号', trigger: 'blur' },
            { min: 3, max: 50, message: '长度在 3 到 50 个字符', trigger: 'blur' }
          ],
          password:[
            { required: true, message: '请输入密码', trigger: 'blur' },
            { min: 3, max: 50, message: '长度在 3 到 50 个字符', trigger: 'blur' }
          ]
        },
        browser:"",
        buttonClicked:false
      }
    },

    mounted(){
        if(window.localStorage.getItem("stuName") === null){
            if(this.$route.path !== '/'){
                this.$router.push({
                    name:'Login'
                })
            }
        }else if(window.localStorage.getItem("stuCode").length < 13){
            if(this.$route.path !== '/Leader'){
                this.$router.push({
                    name:'leader'
                })
            }
        }else if(window.localStorage.getItem("stuCode").length >= 13){
            this.$show = false
            if(this.$route.path !== '/Sign'){
                this.$router.push({
                    name:'sign'
                })
            }
        }

        this.getIp();
        this.startTimer();
        this.Timer();
    },

    methods: {
         checkIsMobile() {
            const isMobile = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
            if (isMobile) {
                this.$message.warning("禁止使用手机端登录!!")
                this.buttonClicked = true
            // 是手机设备
            // 在这里执行您希望在手机设备上执行的逻辑
            } else {
                
            }
        },

        Timer() {
            // 获取当前时间
            this.currentTime = new Date().toLocaleTimeString();

            // 计算下一个零点的时间差（毫秒）
            const now = new Date();
            const tomorrow = new Date(now.getFullYear(), now.getMonth(), now.getDate() + 1);
            const timeUntilTomorrow = tomorrow - now;

            // 设置定时器，在到达下一个零点时调用方法
            setTimeout(() => {

                axios.get("/loginRecord").then((res)=>{
                })

                this.dailyMethod(); // 调用需要执行的方法
                this.Timer();  // 重新启动计时器，进入下一个24小时周期
            }, timeUntilTomorrow);
        },

        dailyMethod() {
            // 每天调用的方法
            console.log('执行每天调用的方法');
        },

        startTimer() {


            setTimeout(() => {
                window.localStorage.removeItem("stuCode");
                window.localStorage.removeItem("stuName");
            }, 5*60*1000); // 1 hour in milliseconds

            setTimeout(() => {
                window.location.reload();
            },5*60*1000 + 1000);
            
        },
                
        getIp(){
            axios.post("/ip").then((res)=>{
                if(res.data.code === 202390){
                    this.$message.error(res.data.data)
                    this.buttonClicked = true
                }else{
                    
                }
            })
        },


        login(formName){
            this.buttonClicked =true;
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    const parser = new UAParser();

                    // 解析浏览器信息
                    const browserInfo = parser.getBrowser();
                    const browserName = browserInfo.name;

                    console.log(browserName);

                    if(browserName === "Edge"){
                        this.browser = "Edge"
                    }else if(browserName === "Firefox"){
                        this.browser = "Firefox"
                    }else if(browserName === "Google"){
                        this.browser = "Google"
                    }else{
                        this.browser = "1"
                    }

                    if(this.browser !== 1){
                        axios.post("/stuLogin/"+this.browser,this.ruleForm).then((res)=>{
                            if(res.data.code === 202391){
                                this.$message.success("登录成功!欢迎"+ res.data.data.stuName +"同学,来到起点工作室!");

                                setTimeout(() => {
                                    this.browser = ""
                                    window.localStorage.setItem('stuName',res.data.data.stuName),
                                    window.localStorage.setItem('stuCode',res.data.data.stuCode),
                                    window.localStorage.setItem('ComProof',"1"),

                                    this.$show = false
                                    if(res.data.data.isExist === 2){
                                        this.$router.push({
                                            name:'leader'
                                        })
                                    }else{
                                        if(this.$route.path !== '/Sign'){
                                            this.$router.push({
                                                name:'sign'
                                            })
                                        }
                                        window.location.reload();
                                    }
                                    
                                }, 400);

                            }else if(res.data.code === 202390){
                                this.$message.error("登录失败!学号或者密码错误!")
                                setTimeout(() => {
                                    this.buttonClicked = false
                                }, 200);
                            }else if(res.data.code === 202366){
                                this.$message.warning(res.data.data)
                                setTimeout(() => {
                                    this.buttonClicked = false
                                }, 200);
                            }
                        })
                    } else {
                        this.$message.error("请输入正确的格式!")
                    }
                }
            });
        }

    }

  }
</script>

<style>
    *{
        margin: 0;
        padding:0
    }
    header{
        background-color: rgb(195, 212, 223);
    }
    main{
        height: 600px;
    }
    footer{
        margin-top: 10px;
        background-color: rgb(195, 212, 223);
    }
    #left{
        width: 200px;
        height: 500px;
        float: left;
        margin-left: 350px;
    }
    #middle{
        width: 200px;
        height: 500px;
        float: left;
    }
    #right{
        width: 350px;
        height: 500px;
        margin-left: 45px;
        border: 1px solid gainsboro;
        border-radius: 10px;
        float: left;
    }
    #saying{
        font-size: 30px;
        color:lightskyblue;
    }
    p{
        font-size:15px;
    }
    #top1{
        
        margin-left: 130px;
        font-size: 21px;
    }
</style>