
<template>
  <div>
      <div>
        <el-menu  class="el-menu-demo" mode="horizontal" @select="handleSelect">
          <el-menu-item index="1" @click="showTimeTable">签到表</el-menu-item>
          <el-menu-item index="2" @click="showLateRecord">本周迟到记录</el-menu-item>
          <el-menu-item index="3" @click="showRule">工作室制度</el-menu-item>

          <div style="float:right;font-size:30px;margin-top:5px">
            <el-button round @click="exit">退出</el-button>
            欢迎! {{stuName}}同学!
          </div>
        </el-menu>
        <div style="margin-top:0px;margin-left:600px;">
          <img src="../picture/5.jpg" style="width:200px;height:50px;background-color:transparent;">
        </div>
      </div>
      <div>
        <keep-alive>
          <router-view></router-view>
          <time-table></time-table>
        </keep-alive>
        
      </div>
  </div>
</template>

<script>
import TimeTable from './TimeTable.vue'

export default {
  components: { TimeTable },
    name:'sign',
    data() {
        return {
          stuName:'',
          boolean:true,
        }
    },
    mounted(){
      this.stuName = window.localStorage.getItem('stuName')
    },
    
    methods: {
      
      showTimeTable(){
        if(this.$route.path !== '/TimeTable'){
          this.boolean = false,
          this.$router.push({
            name:'timetable'
          })
        }
      },

      showRule(){
        if(this.$route.path !== '/Rule'){
          this.boolean = false,
          this.$router.push({
            name:'rule'
          })
        }
      },
      showLateRecord(){
        if(this.$route.path !== '/LateRecordInf'){
          this.boolean = false,
          this.$router.push({
            name:'lateRecordInf'
          })
        }
      },
      handleSelect(key,keyPath){
        console.log(key,keyPath);
      },

      exit(){
        this.$show = true
        this.$message.info("退出成功!")   

        setTimeout(() => {
          window.localStorage.removeItem("stuCode");
          window.localStorage.removeItem("stuName");
        }, 400);

        setTimeout(() => {
          window.location.reload();
        },500);
        
      }
      
    }
}   
</script>

<style>
  #top{
    margin-top: 10px;
    margin-bottom: 10px;
    padding-left: 400px;
  }
  #content{
    float: left;
  }
</style>