<template>
  <div>
        <el-table
        :data="LateOneInf"
        style="width:1200px;margin-left:150px"
        >

            <el-table-column
                prop="stuName"
                label="学生姓名"
                width="300">
            </el-table-column>

            <el-table-column
                prop="dayWeek"
                label="周数"
                width="300">
            </el-table-column>

            <el-table-column
                prop="classToClass"
                label="节次"
                width="300">
            </el-table-column>

            <el-table-column
                prop="conditions"
                label="原因"
                width="300">
            </el-table-column>

        </el-table>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    data() {
        return {
            LateOneInf:[]
        }
    },
    mounted(){
        this.getLateInf();
    },
    methods:{
        getLateInf(){
            axios.get("/lateRecord/"+window.localStorage.getItem('stuCode')).then((res)=>{
                if(res.data.code === 202391){
                    this.LateOneInf = res.data.data
                }else{
                    this.$message.info("当前还未有未签和迟到记录!请保持哦!")
                }
            })
        }
    }

}
</script>

